"use client";

import { useEffect } from "react";
import { useRouter } from "next/navigation";
import { Loader2 } from "lucide-react";

export default function MaterialsAllocatePage() {
  const router = useRouter();

  useEffect(() => {
    // Redirect to the actual allocations page
    router.replace("/dashboard/materials/allocations/new");
  }, [router]);

  return (
    <div className="flex items-center justify-center min-h-[400px]">
      <div className="text-center">
        <Loader2 className="h-8 w-8 animate-spin mx-auto mb-4" />
        <p className="text-muted-foreground">Redirecting to material allocation...</p>
      </div>
    </div>
  );
}